@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
:root{
  font-family: 'Poppins','sans-serif';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  overflow:auto;

  /* Global Variables */
  --PrimaryColor : #6FCF97;
  --Black : #333333;
  --Grey : #BDBDBD;
  --White : #ffffff;

  /* BreakPoints */
  --Phone: 480px;
  --Tablet: 768px;
}

body {
  margin: auto;
  
}

h1, h2, h3, h4, h5{
  font-weight: 400;
  margin: 0;
}

h1 {
  font-size: 0.9rem;
  color : #BDBDBD;
  color : var(--Grey);

}

h2 {
  font-size: 1.2rem;
  font-weight: 500;
}

.card{
  padding: 1.5rem;
  margin-left: 1rem;
  margin-right: 1rem;
  margin-top: 1rem;
  background-color: #6FCF97;
  background-color: var(--PrimaryColor);
  border-radius: 5px;
  color: #ffffff;
  color: var(--White);
  line-height: 120%;
}


button{
  text-decoration: none;
  border: none;
  border-radius: 5px;
  background-color: #6FCF97;
  background-color: var(--PrimaryColor);
  color: #ffffff;
  color: var(--White);
  height: 2.5rem;
  width: 9rem;
  font-size: 1rem;
  font-family: "Poppins";
  cursor:pointer
}
.active_class{
  color: #6FCF97;
  color: var(--PrimaryColor);
}

nav{
  background-color:#fff !important;
  margin-bottom:0px;
  margin-left: 2rem;
  margin-right: 2rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  align-items: center;
  display: flex;
  justify-content: space-between;
}

nav a {
 font-size: 1rem;
 display: flex;
 align-items: center;
 text-decoration: none;
 color: var(--Black);
}

nav a .title{
  padding-left: 1rem;
  line-height: 1.2;
} 

nav  ul{
  display: flex;
  list-style-type: none;
  padding-right: 6rem;
}

.links{
  font-size: 1.1rem;
  margin-left: 4rem;
  padding:5px;
  border-radius:4px;
  cursor:pointer;
}

.nav-brand{ 
 color:blue;
 font-size:1.9rem;
} 

.logo{
  height:50px;
  width: 50px;
}

@media(max-width: 700px){
  
   ul {
    z-index: 10;
    position: fixed;
    top: 0;
    right: 0;
    padding-right: 0 !important;
    margin: 0;
    height: 100vh;
    width: 100vw;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: -webkit-clip-path cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s;
    transition: clip-path cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s;
    transition: clip-path cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s, -webkit-clip-path cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s;
    -webkit-clip-path: circle(0 at 89% 7%);
            clip-path: circle(0 at 89% 7%);
  }
  
  .active{
    -webkit-clip-path: circle(100%);
            clip-path: circle(100%);
  }

  li {
    min-width: 4rem;
    margin-left: 0px !important;
    margin-bottom: 0.8rem;
    font-size: 1.3rem !important;
  }

  nav {
    margin-left: 1rem;
    margin-right: 1rem;
  }
  
  .title{
    display: none;
  }

  .nav-menu-btn {
    z-index: 11;
    height: 0.8rem;
    width: 2rem;
    margin-right: 0rem;
    position: relative; }
    .nav-menu-btn > div {
      height: 3px;
      border-width: 1px;
      width: 100%;
      background-color: var(--Black);
      position: absolute;
      transition: all 0.2s cubic-bezier(0.1, 0.82, 0.76, 0.965); }
      
      .nav-menu-btn > div:first-of-type {
      top: 0; }
    
      .nav-menu-btn > div:last-of-type {
      bottom: 0; }
  
      .nav-menu-btn .nav-menu:checked + .first {
        background-color: var(--Black);
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
        top: 5px; }
  
      .nav-menu-btn .nav-menu:checked + .first + .second {
        background-color: var(--Black);
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
        bottom: 5px; }
  
      .nav-menu-btn .nav-menu {
        width: 100%;
        height: 100%;
        position: relative;
        cursor: pointer;
        z-index: 2;
        -webkit-touch-callout: none;
        -webkit-tap-highlight-color: none;
        -webkit-user-select: none;
            -ms-user-select: none;
                user-select: none;
        position: absolute;
        opacity: 0;
      }
}

@media (min-width: 700px) {
  .nav-menu-btn{
    display: none;
  }
}

.item-tray {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 1rem;
  overflow-y: hidden;
  padding: 5px;
  margin-left: 1rem;
  margin-right: 1rem;
}

.chip-tray{
  margin-left: 2rem;
}

.search{
  font-family: Poppins;
  color: rgba(0, 0, 0, 0.24);
  width: 300px;
  height: 45px;
  font-size: 16px;
  border-radius: 10px;
  padding-left: 1rem;
  border-style: none;
}

p{
  margin-bottom: 3rem;
}

.content{
  display: flex;
  flex-direction: column;
}

.header{
  width: auto;
  height: auto;
  padding: 2rem;
  background-color: grey;
  margin-left: 1rem;
  margin-right: 1rem;
  display: flex;
  border-radius: 10px;
  justify-content: space-between;
}

.title-text{
  font-size: 36px;
}

.flex{
  display: flex;
  margin-left: 1rem;
  margin-right: 1rem;
}

.section{
  display: flex;
  flex-direction: column;
  width: calc(100vw * 0.5) !important;
  margin-bottom: 0.5rem;
}

.card-md {
  width: auto;
  height: 100px;
  padding: 20px;
  border-radius: 5px;
  /* margin-right: 20px; */
  display: flex;
  align-items: flex-end;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;

}

.card-lg {
  width: auto;
  height: 150px;
  padding: 20px;
  border-radius: 5px;
  /* margin-right: 20px; */
  margin-bottom: 15px;
  align-items: flex-end;
  display: flex;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;

}

.card-title {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 16px;
  color: black;
  text-align: center;
  border-radius: 10px;
  background-color: #f1f1f1;
}

.head {
  margin-top: 2rem;
  margin-bottom: 2rem;
  margin-left: 1rem;
  margin-right: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 480px) {
  .item-tray {
    width: calc(100vw - 2rem - 10px);
    display: flex;
    flex-direction: column;
    margin-left: 1rem;
    margin-right: 1rem;
  }

  .flex{
    flex-direction: column;
  }

  .section{
    width: calc(100vw - 2rem - 10px) !important;
  }

  .search{
     position: absolute;
     top: 90px;
     left: 0;
     margin-left: 1rem;
     width: calc(100vw - 3rem - 6px);
     border: 3px solid rgba(0, 0, 0, 0.14);
     outline: none;
  }

  .header{
    margin-top: 82px;
  }
}



.showslider{
    height:500px;
    overflow:hidden;
    margin-left: 1rem;
    margin-right: 1rem;
    border-radius: 5px;
    position: relative;
}

.navigation{
    position:absolute;
    bottom:2%;
    left: 50%;
    /* margin-left: -50%; */
    -webkit-transform:translateX(-50%);
            transform:translateX(-50%);
    display:flex;
}
.bar{
    width: 10px;
    height: 10px;
    border: 2px solid var(--White);
    margin: 10px;
    border-radius: 10px; 
    transition: 0.5s;
    cursor:pointer;
}
.bar:hover{
    background: var(--White) ;
}
input[name="a"]{
    position:absolute;
    visibility:hidden;
}
.slides{
   width:500%;
   height:100%;
   display:flex;
}
.slide{
   width:20%;
   transition:0.6s;
}
.slide img{
    width: 100%;
    height: 500px;
}
#a1:checked ~ .s1{
    margin-left:0%;
}

#a2:checked ~ .s1{
    margin-left:-20%;
}
#a3:checked ~ .s1{
    margin-left:-40%;
}

@media(max-width: 480px){
  .slide img{
    width: auto;
    height: 100%;
  }
} 

